import axios from 'axios';

export function getDiscList (limit) {
  const url = '/top/playlist/highquality';

  const data = {
    limit: limit
  };

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  });
}



