<template>
  <scroll class="disc"
          :data="data"
          :pulldown="pulldown">
    <div>
      <ul>
        <li class="disc-item" v-for="item in data">
          <img v-lazy="item.coverImgUrl" class="cover">
          <span class="title" v-html="item.name"></span>
        </li>
      </ul>
    </div>
  </scroll>
</template>

<script>
  import Scroll from 'base/scroll/scroll';
  import {getDiscList} from 'api/disc';

  export default {
    data () {
      return {
        data: [],
        pulldown: true
      };
    },
    created () {
      getDiscList(12).then((res) => {
        this.data = res.playlists;
      });
    },
    components: {
      'scroll': Scroll
    }
  };
</script>

<style lang="stylus">
  @import '~common/stylus/variable.styl'

  .disc
    position: absolute
    top: 55px
    bottom: 0
    left: 0
    right: 0
    overflow: hidden
    ul
      text-align center
      font-size: 0
      .disc-item
        display: inline-block
        width: 49%
        padding: 10px
        box-sizing border-box
        vertical-align: top
        .cover
          display: block
          margin: 0 auto 10px auto
          width: 90%
          height: auto
          background: #efefef
        .title
          color: $color-text
          font-size: $font-size-medium
          line-height: $font-size-large
</style>



